/* Timeline Section */
.timeline-section {
  background: #f8f9fa;
  padding: 40px 0;
}

.timeline {
  max-width: 800px;
  margin: 0 auto;
  position: relative;
}

.timeline::before {
  content: '';
  position: absolute;
  left: 30px;
  top: 0;
  bottom: 0;
  width: 2px;
  background: #1a1a1a;
  z-index: 1;
}

.timeline-item {
  display: flex;
  margin-bottom: 3rem;
  position: relative;
}

.timeline-marker {
  position: relative;
  z-index: 2;
  margin-right: 2rem;
  flex-shrink: 0;
}

.timeline-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: white;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.timeline-item:hover .timeline-icon {
  transform: scale(1.1);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
}

.timeline-content {
  flex: 1;
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.08);
  position: relative;
  transition: all 0.3s ease;
}

.timeline-item:hover .timeline-content {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.12);
}

.timeline-content::before {
  content: '';
  position: absolute;
  left: -10px;
  top: 20px;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 10px solid white;
}

.timeline-year {
  font-size: 0.9rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 0.5rem;
}

.timeline-title {
  font-size: 1.3rem;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 0.5rem;
  line-height: 1.3;
}

.timeline-position {
  font-size: 1rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 0.5rem;
}

.timeline-company {
  font-size: 0.9rem;
  font-weight: 500;
  color: #666;
  margin-bottom: 1rem;
}

.timeline-projects {
  margin: 1rem 0;
}

.timeline-projects h5 {
  font-size: 0.9rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 0.5rem;
}

.timeline-projects ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.timeline-projects li {
  font-size: 0.85rem;
  color: #555;
  margin-bottom: 0.3rem;
  padding-left: 1rem;
  position: relative;
}

.timeline-projects li::before {
  content: '•';
  color: #1a1a1a;
  position: absolute;
  left: 0;
}

.timeline-description {
  color: #555;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.timeline-technologies {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tag {
  background: #f8f9fa;
  color: #1a1a1a;
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
  border: 1px solid #e9ecef;
  transition: all 0.3s ease;
}

.tech-tag:hover {
  background: #1a1a1a;
  color: white;
  transform: translateY(-2px);
}

/* Animation for timeline items */
.timeline-item {
  opacity: 0;
  transform: translateX(-30px);
  animation: slideInLeft 0.6s ease forwards;
}

.timeline-item:nth-child(1) { animation-delay: 0.1s; }
.timeline-item:nth-child(2) { animation-delay: 0.2s; }
.timeline-item:nth-child(3) { animation-delay: 0.3s; }
.timeline-item:nth-child(4) { animation-delay: 0.4s; }
.timeline-item:nth-child(5) { animation-delay: 0.5s; }
.timeline-item:nth-child(6) { animation-delay: 0.6s; }
.timeline-item:nth-child(7) { animation-delay: 0.7s; }
.timeline-item:nth-child(8) { animation-delay: 0.8s; }
.timeline-item:nth-child(9) { animation-delay: 0.9s; }
.timeline-item:nth-child(10) { animation-delay: 1.0s; }

@keyframes slideInLeft {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Mobile Styles */
@media (max-width: 768px) {
  .timeline::before {
    left: 20px;
  }
  
  .timeline-item {
    flex-direction: column;
    margin-bottom: 2rem;
  }
  
  .timeline-marker {
    margin-right: 0;
    margin-bottom: 1rem;
    align-self: flex-start;
  }
  
  .timeline-icon {
    width: 50px;
    height: 50px;
    font-size: 1.2rem;
  }
  
  .timeline-content {
    padding: 1.5rem;
    margin-left: 0;
  }
  
  .timeline-content::before {
    display: none;
  }
  
  .timeline-title {
    font-size: 1.2rem;
  }
  
  .timeline-technologies {
    gap: 0.3rem;
  }
  
  .tech-tag {
    font-size: 0.75rem;
    padding: 0.2rem 0.6rem;
  }
}

@media (max-width: 480px) {
  .timeline-content {
    padding: 1rem;
  }
  
  .timeline-title {
    font-size: 1.1rem;
  }
  
  .timeline-company {
    font-size: 0.9rem;
  }
  
  .timeline-description {
    font-size: 0.9rem;
  }
}
