/* Project Carousel Section */
.projects {
  padding: 40px 0;
  background: white;
}

.carousel-container {
  max-width: 1000px;
  margin: 0 auto;
  position: relative;
}

.carousel-wrapper {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.carousel-btn {
  background: #1a1a1a;
  color: white;
  border: none;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 10;
  flex-shrink: 0;
}

.carousel-btn:hover {
  background: #000000;
  transform: scale(1.1);
}

.carousel-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.carousel-content {
  flex: 1;
  min-height: 500px;
  display: flex;
  align-items: center;
}

.project-card {
  background: white;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0;
  min-height: 450px;
}

.project-image {
  position: relative;
  overflow: hidden;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.project-card:hover .project-image img {
  transform: scale(1.05);
}

.project-content {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.project-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.project-category {
  background: #1a1a1a;
  color: white;
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
}

.project-period {
  color: #666;
  font-size: 0.9rem;
  font-weight: 500;
}

.project-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 1rem;
  line-height: 1.3;
}

.project-description {
  color: #555;
  line-height: 1.6;
  margin-bottom: 1.5rem;
  flex-grow: 1;
}

.project-technologies {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.tech-tag {
  background: #f8f9fa;
  color: #1a1a1a;
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
  border: 1px solid #e9ecef;
}

.project-role {
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.project-links {
  display: flex;
  gap: 1rem;
}

.project-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: #1a1a1a;
  color: white;
  text-decoration: none;
  border-radius: 25px;
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.project-link:hover {
  background: #000000;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(26, 26, 26, 0.3);
}

.project-type {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.company-badge {
  background: #f8f9fa;
  color: #1a1a1a;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
  border: 2px solid #1a1a1a;
}

.carousel-indicators {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 2rem;
}

.indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background: #e9ecef;
  cursor: pointer;
  transition: all 0.3s ease;
}

.indicator.active {
  background: #1a1a1a;
  transform: scale(1.2);
}

.indicator:hover {
  background: #666;
}

.carousel-counter {
  text-align: center;
  margin-top: 1rem;
  color: #666;
  font-size: 0.9rem;
  font-weight: 500;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .carousel-wrapper {
    flex-direction: column;
    gap: 1rem;
  }
  
  .carousel-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  
  .carousel-btn.prev {
    left: 10px;
  }
  
  .carousel-btn.next {
    right: 10px;
  }
  
  .project-card {
    grid-template-columns: 1fr;
    min-height: auto;
  }
  
  .project-image {
    height: 200px;
  }
  
  .project-content {
    padding: 1.5rem;
  }
  
  .project-title {
    font-size: 1.3rem;
  }
  
  .project-links {
    flex-direction: column;
  }
  
  .project-link {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .carousel-container {
    padding: 0 1rem;
  }
  
  .carousel-btn {
    width: 40px;
    height: 40px;
  }
  
  .project-content {
    padding: 1rem;
  }
  
  .project-meta {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
}
